@keyframes header {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  background: #f7f9fb;
  padding: 0 0 94px;
  max-width: 500px;
  margin: 0 auto;
  position: relative;
}

a,
button {
  transition: all 0.3s;
}
a:active,
button:active {
  opacity: 0.8;
}

.container {
  padding: 0 15px;
}

.nav {
  padding: 6px 0;
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  backdrop-filter: blur(10px);
  background: rgba(244, 244, 244, 0.75);
  z-index: 50;
}
.nav .container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav__logo {
  width: 52px;
}
.nav__logo img {
  width: 100%;
}
.nav__alert {
  position: relative;
  margin: 0 18px 0 auto;
}
.nav__alert span {
  font: 600 12px/1 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  position: absolute;
  top: -10px;
  right: -10px;
  background: #fa8787;
  border-radius: 100%;
}
.nav__blue {
  border: none;
  width: 162px;
  padding: 10px;
  font: 600 14px/1.71429 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  background: url("../img/btn_blue.svg") center no-repeat;
  background-size: contain;
}
.nav__lang {
  position: relative;
  margin: 0 0 0 8px;
}
.nav__lang_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
  border: 1px solid #efeff4;
  border-radius: 10px;
  padding: 10px 10px 10px 13px;
  cursor: pointer;
  background: #fff;
}
.nav__lang_list {
  position: absolute;
  top: calc(100% + 3px);
  left: 0;
  width: 100%;
  border: 1px solid #efeff4;
  border-radius: 10px;
  background: #fff;
  display: grid;
  transition: all 0.3s;
  opacity: 0;
  pointer-events: none;
}
.nav__lang_list a {
  padding: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #efeff4;
}
.nav__lang_list a:last-child {
  border-bottom: none;
}
.nav__lang.active .nav__lang_list {
  opacity: 1;
  pointer-events: all;
}

.header {
  padding: 20px 0;
}
.header h1 {
  font: 700 56px/0.9 "Inter", sans-serif;
  letter-spacing: 0em;
  text-align: center;
  color: #000;
}
.header h1 span {
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.header h1 i {
  font: 400 32px/1.59375 "Inter", sans-serif;
  letter-spacing: 0em;
  color: #78b6a0;
  animation-duration: 1s;
  animation-name: header;
  animation-iteration-count: infinite;
}
.header__text {
  margin: 20px 0 0;
  font: 400 20px/1.2 "Inter", sans-serif;
  letter-spacing: 0.01em;
  text-align: center;
  color: #000;
}
.header__btn {
  margin: 20px 0;
  box-shadow: 0 4px 13px 0 rgba(110, 131, 253, 0.74);
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  width: 100%;
  padding: 24px 10px;
  font: 600 24px/1 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
  border: none;
  border-radius: 10px;
}
.header__price {
  margin: 0;
  border-radius: 10px;
  background: #fff;
  padding: 15px;
  display: grid;
  gap: 10px;
}
.header__price_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__price_name {
  font: 400 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #010101;
}
.header__price_prof {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.header__price_prof p {
  font: 400 14px/1 "Inter", sans-serif;
  color: #707579;
}
.header__price_val {
  font: 600 36px/1 "Inter", sans-serif;
  letter-spacing: 0em;
  text-align: center;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.header__price_arrow {
  border-radius: 10px;
  padding: 10px 12px;
  background: rgba(0, 255, 178, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
}
.header__price_arrow p {
  font: 400 16px/0.875 "Inter", sans-serif;
  color: #02d495;
}
.header__stat {
  margin: 20px 0 0;
  border-radius: 10px;
  padding: 15px;
  background: #fff;
  max-width: 100%;
  overflow-x: auto;
}
.header__stat img {
  width: 100%;
  pointer-events: none;
}
.header__stat canvas {
  width: 100%;
}

.why {
  padding: 20px 0;
  background: #fff;
}
.why h2 {
  font: 700 24px/1.3 "Inter", sans-serif;
  letter-spacing: 0.01em;
  text-align: center;
  color: #000;
}
.why h2 span {
  font: 700 24px/1.3 "Inter", sans-serif;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.why__wrapper {
  margin: 15px 0 0;
  display: grid;
  gap: 15px;
}
.why__item {
  padding: 15px 15px 128px;
  border-radius: 10px;
  z-index: 2;
  position: relative;
  overflow: hidden;
}
.why__item p {
  font: 600 20px/1.2 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
}
.why__item p + p {
  margin: 10px 0 0;
  font: 400 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
}
.why__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: bottom center;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}

.quest {
  padding: 40px 0 20px;
}
.quest h2 {
  font: 700 24px/1.29167 "Inter", sans-serif;
  letter-spacing: 0.01em;
  text-align: center;
  color: #000;
}
.quest h2 span {
  font: 700 24px/1.29167 "Inter", sans-serif;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.quest__wrapper {
  margin: 20px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 6px;
}
.quest__num {
  font: 600 36px/1 "Inter", sans-serif;
  letter-spacing: 0em;
  color: #d9d9d9;
}
.quest__row {
  margin: 15px 0 0;
  display: grid;
  grid-template-columns: 1fr 32px;
  gap: 20px;
  align-items: start;
}
.quest__row p {
  font: 600 20px/1.2 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #000;
}
.quest__arrow {
  width: 32px;
  height: 32px;
  border-radius: 100%;
  background: #efeff4;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
}
.quest__arrow path {
  transition: all 0.3s;
}
.quest__text {
  margin: 0;
  font: 400 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #010101;
  transition: all 0.5s;
  max-height: 0;
  overflow: hidden;
}
.quest__item {
  border: 1px solid #efeff4;
  border-radius: 10px;
  padding: 15px;
  background: #fff;
}
.quest__item.active .quest__arrow {
  background: #010101;
  transform: rotate(45deg);
}
.quest__item.active .quest__arrow path {
  fill: white;
}
.quest__item.active .quest__text {
  margin: 15px 0 0;
  max-height: 300px;
}

.nav-fix {
  padding: 10px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  backdrop-filter: blur(10px);
  background: rgba(244, 244, 244, 0.75);
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  z-index: 20;
}
.nav-fix p {
  font: 500 12px/2 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #010101;
  text-align: center;
  transition: all 0.3s;
}
.nav-fix svg {
  margin: 0 auto;
  display: block;
}
.nav-fix svg path {
  fill: #010101;
  transition: all 0.3s;
}
.nav-fix .active p {
  color: #0098ea;
}
.nav-fix .active path {
  fill: #0098ea;
}

.balance {
  padding: 20px 0;
}
.balance .container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
.balance__item {
  border: 1px solid #efeff4;
  border-radius: 10px;
  padding: 15px;
  background: #fff;
}
.balance__logo {
  display: grid;
  grid-template-columns: 28px 1fr;
  gap: 5px;
}
.balance__logo img {
  width: 100%;
}
.balance__logo p {
  font: 600 14px/1 "Inter", sans-serif;
  color: #010101;
}
.balance__logo p + p {
  margin: 3px 0 0;
  font: 400 12px/1.16667 "Inter", sans-serif;
  color: rgba(112, 117, 121, 0.4);
}
.balance__val {
  margin: 2px 0 0;
  font: 500 12px/2 "Inter", sans-serif;
  color: #707579;
}
.balance__val span {
  font: 500 18px/1.33333 "Inter", sans-serif;
  letter-spacing: 0.01em;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.swap {
  padding: 20px 0;
  background: #fff;
}
.swap h1 {
  font: 700 24px/1.29167 "Inter", sans-serif;
  letter-spacing: 0.01em;
  text-align: center;
  color: #000;
}
.swap__subtitle {
  margin: 15px 0 0;
  font: 400 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  text-align: center;
  color: #010101;
}
.swap__wrapper {
  margin: 15px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 25px;
}
.swap__item {
  position: relative;
}
.swap__input {
  position: relative;
}
.swap__input input {
  border: 1px solid #d9d9d9;
  border-radius: 10px;
  padding: 17px 150px 17px 9px;
  background: #fff;
  width: 100%;
  box-sizing: border-box;
  font: 400 14px/1 "Inter", sans-serif;
  color: #010101;
}
.swap__func {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 3px;
}
.swap__ton {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
}
.swap__ton p {
  font: 400 16px/0.875 "Inter", sans-serif;
  color: #010101;
}
.swap__ton img {
  width: 20px;
}
.swap__max {
  padding: 8px 12px;
  font: 400 12px/1.16667 "Inter", sans-serif;
  text-align: right;
  color: #0098ea;
  border-radius: 8px;
  background: rgba(0, 152, 234, 0.1);
}
.swap__row {
  position: absolute;
  top: calc(100% + 5px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  left: 0;
  right: 0;
}
.swap__row p {
  font: 400 14px/1 "Inter", sans-serif;
  color: #707579;
}
.swap__row div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
}
.swap__row img {
  width: 14px;
}
.swap__btn {
  width: 32px;
  height: 32px;
  margin: 0 auto;
  display: block;
}
.swap__enter {
  margin: 15px 0 0;
  width: 100%;
  border: none;
  padding: 13px;
  border-radius: 10px;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  font: 600 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.swap__name {
  position: absolute;
  bottom: calc(100% + 5px);
  left: 0;
  font: 400 14px/1 "Inter", sans-serif;
  color: #707579;
}
.swap__name span {
  color: #fa8787;
}

.output {
  margin: 20px 0 0;
  padding: 20px 0;
  background: #fff;
}
.output h2 {
  font: 700 24px/1.29167 "Inter", sans-serif;
  letter-spacing: 0.01em;
  text-align: center;
  color: #010101;
}
.output__wrapper {
  margin: 15px 0 0;
  display: grid;
  gap: 15px;
}
.output .swap__name, .output .swap__row {
  position: relative;
  inset: auto;
}
.output .swap__input {
  margin: 5px 0;
}
.output__btn {
  margin: 15px 0 0;
  width: 100%;
  border-radius: 10px;
  padding: 13px;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  font: 600 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
  border: none;
}

.history {
  padding: 20px 0;
}
.history h2 {
  font: 700 24px/1.29167 "Inter", sans-serif;
  letter-spacing: 0.01em;
  text-align: center;
  color: #000;
}
.history__wrapper {
  margin: 10px 0 0;
  display: grid;
  gap: 10px;
}
.history__item {
  border: 1px solid #efeff4;
  border-radius: 10px;
  padding: 15px;
  background: #fff;
  display: grid;
  grid-template-columns: 32px 1fr auto;
  gap: 10px;
  align-items: center;
}
.history__done {
  width: 32px;
  height: 32px;
  background: rgba(2, 212, 149, 0.1);
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.history__done.error {
  background: rgba(250, 135, 135, 0.1);
}
.history__text p {
  font: 500 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #010101;
}
.history__text p + p {
  font: 500 14px/1.71429 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #707579;
  margin: -2px 0 0;
}
.history__val {
  font: 500 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #000;
}
.history__val.green {
  color: #02d495;
}
.history__val.red {
  color: #fa8787;
}

.task {
  padding: 20px 0;
}
.task__block {
  background: url("../img/task_bg.jpg") center right no-repeat;
  background-size: cover;
  padding: 20px 100px 20px 20px;
  border-radius: 10px;
}
.task__block h2 {
  font: 600 24px/1 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
}
.task__block p {
  font: 400 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
}
.task__wrapper {
  margin: 20px -15px 0;
  padding: 20px 15px;
  background: #fff;
  display: grid;
  gap: 15px;
}
.task__item {
  border: 1px solid #dde3e6;
  border-radius: 10px;
  padding: 15px;
  background: #fff;
}
.task__row {
  display: grid;
  grid-template-columns: 78px 1fr;
  gap: 10px;
}
.task__row img {
  width: 100%;
  border-radius: 8px;
}
.task__row h3 {
  font: 500 16px/1.25 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #000;
}
.task__val {
  margin: 5px 0 0;
  width: max-content;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
  padding: 5px 10px;
  border-radius: 10px;
  background: linear-gradient(90deg, rgba(0, 152, 234, 0.1) 0%, rgba(161, 104, 255, 0.1) 100%);
}
.task__val p {
  font: 600 12px/2 "Inter", sans-serif;
  letter-spacing: 0.01em;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.task__val img {
  max-width: 20px;
}
.task__btn {
  margin: 10px 0 0;
  border-radius: 8px;
  padding: 10px;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  border: none;
  font: 500 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
  width: 100%;
}
.task__done {
  margin: 10px 0 0;
  background: rgba(2, 212, 149, 0.1);
  border-radius: 8px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font: 500 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #02d495;
  border: none;
  width: 100%;
}
.task__error {
  margin: 10px 0 0;
  background: rgba(250, 135, 135, 0.1);
  border-radius: 8px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font: 500 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fa8787;
  border: none;
  width: 100%;
}
.task #rotating-part {
  transform-origin: center;
  animation: rotate 2s linear infinite;
}
.task__check {
  margin: 10px 0 0;
  background: rgba(0, 152, 234, 0.1);
  border-radius: 8px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font: 500 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #0098ea;
  border: none;
  width: 100%;
}

.finance {
  padding: 20px 0 15px;
}
.finance__block {
  background: url("../img/finance_bg.jpg") center no-repeat;
  background-size: cover;
  padding: 15px;
  border-radius: 10px;
}
.finance h2 {
  font: 600 20px/1.2 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
}
.finance__text {
  margin: 10px 0 0;
  font: 400 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
}
.finance__link {
  margin: 80px 0 0;
  border: 1px solid #efeff4;
  border-radius: 10px;
  padding: 15px;
  background: #fff;
}
.finance__link h3 {
  font: 600 20px/1.2 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #000;
  text-align: center;
}
.finance__link p {
  margin: 5px 0 0;
  font: 400 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #010101;
  text-align: center;
}
.finance__row {
  display: grid;
  grid-template-columns: 1fr 50px;
  margin: 5px 0 0;
  gap: 15px;
}
.finance__row button {
  width: 100%;
  border: none;
  border-radius: 10px;
  padding: 13px 0;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  font: 600 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
}
.finance__row a {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #efeff4;
  border-radius: 10px;
}

.team {
  padding: 15px 0 5px;
}
.team h2 {
  font: 700 24px/1.29167 "Inter", sans-serif;
  letter-spacing: 0.01em;
  text-align: center;
  color: #000;
}
.team__ref {
  margin: 15px 0 0;
  border-radius: 10px;
  padding: 15px;
  background: url("../img/team_bg.jpg") center no-repeat;
  background-size: cover;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
.team__ref_item {
  border: 1px solid #efeff4;
  border-radius: 10px;
  padding: 15px;
  background: #fff;
}
.team__ref_item p {
  font: 600 14px/1 "Inter", sans-serif;
  color: #010101;
}
.team__ref_item p + p {
  margin: 5px 0 0;
  font: 500 12px/2 "Inter", sans-serif;
  color: #707579;
}
.team__ref_item p + p span {
  font: 500 18px/1.33333 "Inter", sans-serif;
  letter-spacing: 0.01em;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.team__wrapper {
  margin: 15px 0 0;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
  justify-content: center;
}
.team__item {
  border: 1px solid #dde3e6;
  border-radius: 10px;
  padding: 10px 10px 0px 10px;
  background: #fff;
  width: calc(50% - 5px);
}
